<template>
  <div class="settings">
    <div class="container-fluid">
      <div class="container">
        <h1>Settings</h1>
        <div class="row mt-4">
          <div class="settings-line d-flex justify-content-around form-inline">
            <span>Internet radio language</span>
            <select v-model="radio_language" class="form-control">
              <option value="pl">PL</option>
              <option value="eng">ENG</option>
            </select>
          </div>
        </div>
      </div>

    </div>
  </div>
</template>

<script>
export default {
  name: 'settings',
  data() {
    return {
      radio_language: '',
    };
  },

  mounted() {
    if (localStorage) {
      this.radio_language = localStorage.radio_language;
    }
  },

  watch: {
    radio_language() {
      if (localStorage) {
        localStorage.setItem('radio_language', this.radio_language);
        this.api.radio_language = localStorage.radio_language;
      }
    },
  },
};

</script>

<style scoped>
  .settings-line {
    position: relative;
    width: 100%;
    min-height: 50px;
    text-align: left;
    padding: 20px;
  }

  .settings-line:hover {
    background-color: rgba(0, 0, 0, 0.1);
    transition: 300ms;
  }

</style>
